.popular-movies{
  display:grid;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  grid-gap:2rem 1rem;
  
}
img{
  width:100%;
  height: 40vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom:1rem

}
h2{
  font-size: 0.8rem;
}
.filter-container button{
  margin-right: 2rem;
  min-width: 5rem;
  padding:0.5rem 1rem;
  border:none;
  background: skyblue;
  color:black;
  border-radius: 1rem;
  border: gold;
  font-weight: bold;
  cursor: pointer;
}
button.active{
  background: rgb(65,98,168);
  color:white;
  
}